h3
{
	color:#767676;
}
h2
{
	color:#ea4545;
}
.pd-b
{
	padding-bottom:30px;
}
.bg-red
{
	background-color:#ea4545;
}
.center
{
  	position: fixed;
  	top: 50%;
 	left: 50%;
  	transform: translate(-50%, -50%);
    padding: 15px;
    background: #fff;
}
.form-control
{
	background-color: #f5f5f5;
    box-shadow: none;
    color: #565656;
    font-family: rubik,sans-serif;
    font-size: 1rem;
    line-height: 1.43;
    min-height: 3.5em;
    padding: 1.07em .5em;
    border: 1px solid #e8e8e8;
}
.start-btn
{
	cursor:pointer;
	border-radius: 100px;
	font-size: 17px;
	font-weight: bold;
	background-color:#ea4545;
	color:#e8e8e8;
}
.pt_10
{
	padding-top:10px;
}
.title
{
	text-align: center;
    font-family: 'Rubik',sans-serif;
}
.h-size
{
	font-size:30px;
}
.t-size
{
	font-size:20px;
}
.l-size
{
	width:50px;
}
.skip-btn
{
	cursor:pointer;
	font-size:25px;
	transform:rotate(45deg);
	transition: 0.3s;
	font-size:20px;
	color:#ea4545;
	background-color:white;
	border-radius:50px;
	margin:10px;
	padding:10px;
}
.send-btn
{
	padding:20px;
	transition: 0.3s;
	font-size:20px;
	color:white;
	background-color:#ea4545;
	width: 10%;
	height: 57px;
	padding-left: 10px;
}
.img-btn
{
	padding:20px;
	transition:0.3s;
	font-size:20px;
	color:white;
	background-color:#ea4545;
	width:8%;
	height:57px;
	padding-left:5px;
}
.f-text
{
	font-family: 'Rubik', sans-serif;
}
.r-text
{
	color:#ea4545;
}
.align-right
{
	text-align:right;
}
.w-70
{
	width:70%;
}
#message
{
	border-top:0.5px solid #ea4545;
	border-radius:0px;
}
.send-container
{
	text-align:center;
	background:#ea4545;
}
#chat
{
	max-height:400px;
	overflow-y:hidden;
}